.container {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    background: #5F41E4;
  }
  .signup-container {
    margin: 0 auto;
    width: 350px;
    padding: 2rem 1.5rem;
    border-radius: 0.5rem;
    background: #fff;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  }
  .signup-container .form-title {
    text-align: center;
    font-size: 1.37rem;
    font-weight: 600;
    margin-bottom: 1.87rem;
  }

  .signup-form .input-wrapper {
    height: 54px;
    width: 100%;
    position: relative;
    margin-bottom: 1.5rem;
  }
  .signup-form .input-field {
    width: 310px;
    height: 100%;
    outline: none;
    font-size: 1.06rem;
    border-radius: 0.31rem;
    border: 1px solid #bfb3f2;
    padding: 0px 1.25rem 0 1.12rem;
    transition: 0.2s ease;
  }
  .signup-form .input-field:focus {
    border-color: #5F41E4;
  }
  .signup-form .input-field::placeholder {
    color: #9284c8;
  }
  .signup-form .input-wrapper i {
    position: absolute;
    top: 50%;
    height: 100%;
    display: flex;
    color: #a395e0;
    align-items: center;
    transform: translateY(-50%);
    transition: 0.2s ease;
  }
  .signup-form .input-wrapper i:not(.eye-icon) {
    left: 0.93rem;
    pointer-events: none;
  }
  .signup-form .input-field:focus ~ i:not(.eye-icon) {
    color: #5F41E4;
  }
  .signup-form .input-wrapper i.eye-icon {
    display: none;
    right: 0.93rem;
    color: #917DE8;
    cursor: pointer;
    font-size: 1.25rem;
  }
  .signup-form .input-wrapper .input-field:valid ~ .eye-icon {
    display: flex;
  }
  .signup-form .forgot-password-link {
    display: block;
    width:fit-content;
    margin-top: -0.44rem;
  }
  .signup-form .signup-button {
    border: none;
    outline: none;
    width: 100%;
    height: 54px;
    color: #fff;
    font-size: 1.125rem;
    font-weight: 500;
    cursor: pointer;
    margin-top: 2.19rem;
    border-radius: 0.31rem;
    background: #5F41E4;
    transition: 0.3s ease;
    margin: 0px;
  }
  .signup-form .signup-button:hover {
    background: #4320df;
  }
  .signup-container .signup-text {
    text-align: center;
    font-size: 1.06rem;
    font-weight: 500;
    margin: 1.75rem 0 0.31rem;
  }
  .signup-container a {
    color: #5F41E4;
    font-weight: 500;
    text-decoration: none;
  }
  .signup-container a:hover {
    text-decoration: underline;
  }
  .sign-up-text{
    color: black;
  }

  * {
    text-decoration: none;
}

.navbar {
    background: #24252A;
    font-family: calibri;
    padding-right: 15px;
    padding-left: 15px;
}

.navdivv {
    display: flex;
    align-items: center;
    justify-content:space-between;

    padding: 15px 5%;
}
.logo a {
    font-size: 35px;
    font-weight: 500;
    color: white;
}


button {
    background-color: rgba(0, 136, 169, 1);
    margin-left: 10px;
    border-radius: 50px;
    border: none;
    cursor: pointer;
    padding: 9px 25px;
    transition: all 0.3s ease 0s;
}
button:hover{
    background-color: rgba(0, 136, 169, 0.8);

}
button a {
    color: #edf0f1;
    font-weight: bold;
    font-size: 15px;
}
.customer-registration-btn, .tasker-registration-btn{
  margin-left: 50px;
  margin-bottom: 15px;
  background-color: #3a3a3a;
  border-radius: 10px;
  color: white;
transition: 0.3s ease;
}
.customer-registration-btn:hover, .tasker-registration-btn:hover{
  background-color:rgb(129, 128, 128);
}



.form-group {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.gender-options {
    display: flex;
    align-items: center;
    gap: 1.5rem;
    margin-bottom: 1rem;
}

.gender-label {
    font-weight: 500;
    color: #333;
}

.radio-group {
    display: flex;
    align-items: center;
    gap: 0.5rem;
}

.gender-input {
    cursor: pointer;
    width: 16px;
    height: 16px;
    accent-color: #5F41E4;
}

.radio-label {
    cursor: pointer;
    font-size: 0.95rem;
    color: #333;
}

.city-select-wrapper {
  display: flex;
  flex-direction: column;

}

.city-select {
  padding: 0.5rem;
  border-radius: 6px;
  border: 1px solid #ccc;
  margin-bottom: 10px;
}

.profile-image-upload {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 20px;
    margin-bottom: -30px;
    margin-left: 80px;
}

.image-preview {
    width: 120px;
    height: 120px;
    border-radius: 60px;
    overflow: hidden;
    margin-bottom: 15px;
    border: 3px solid #0088A9;
}

.image-preview img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.file-input-label {
    background-color: #0088A9;
    color: white;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s;
    font-size: 14px;
}

.file-input-label:hover {
    background-color: #006d87;
}

.file-input {
    display: none;
}

.type-options {
    display: flex;
    align-items: center;
    gap: 1.5rem;
    margin-bottom: 1rem;
}

.type-label {
    font-weight: 500;
    color: #333;
}

.type-input {
    cursor: pointer;
    width: 16px;
    height: 16px;
    accent-color: #5F41E4;
}

.company-fields {
    margin-top: 1rem;
    padding: 1rem;
    background-color: #f8f9fa;
    border-radius: 8px;
    border: 1px solid #e9ecef;
}

.license-upload {
    margin-top: 1rem;
}

.license-preview {
    width: 200px;
    height: 150px;
    margin: 0 auto 1rem;
    border: 2px solid #5F41E4;
    border-radius: 8px;
    overflow: hidden;
}

.license-preview img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.login-link {
    color: #5F41E4;
    font-weight: 500;
    text-decoration: none;
    transition: color 0.2s ease;
}

.login-link:hover {
    color: #4320df;
    text-decoration: underline;
}
